@import '../../../../scss/configs/breakpoints.scss';

.ant-modal-content {
    
    & .ant-modal-close {
      top: 18px;
      right: -11px;
      background-color: #c62626;
      border-radius: 50%;
      & .ant-modal-close-x {
        font-size: 12px;
        color: #fff;
      }
    }
    .ant-modal-body {
        & h3 {
            text-align: center;
            font-size: var(--font-size-base);
            color: #878787;
            font-weight: 700;
            margin: 0;
        }
        & .form-control {
            display: flex;
            flex-flow: wrap;          
            margin: 7px 3px 3px 0;
          justify-content: space-around;
            &.picture, .ant-upload-list{
                display: flex;
                justify-content: center;
            }
            &.ant-form-item,
            .ant-input,
            .ant-col, 
            label{
                color: #878787;
                padding: 2px;
                margin: 0;
                flex: auto;
                font-family: "Menticula";
                &-control-input {
                    &-content {
                        & .ant-input-affix-wrapper{
                            padding: 5px 12px;
                            font-size: medium;
                            border-radius: 25px;
                            width: 100%;
                        }
                    }                    
                }&.ant-input{
                    padding: 7px;
                    font-size: Light;
                }
            }
        }
    }

    & .ant-modal-footer {
        position: absolute;
        margin: 0px;
        bottom: -22px;
        width: 100%;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        & .ant-btn {
          border: none;
          font-weight: bold;
          width: auto;
          height: auto;
          border-radius: 7px;
          background-color: var(--primary-color--light);
          color: white;
          font-size: var(--font-size-base);
          // margin: 0px 5px;
          cursor: pointer;
          transition: all 0.3s ease;
          &:hover {
            color: #ffad02;
          }
          & span {
            padding: 3px 12px;
          }
        }
      }
}


